<script>
import echarts from "echarts";
import TreeOrg from "../../common/TreeOrg.vue";
var rundetailinterval=null;
export default {
    data() {
        return {
         
          treeData: [
              {
                label: '光伏项目01',
                children: [
                    {
                        id:'rsd1',
                        label: '光伏站A',
                    },
                    {
                        id:'rsd2',
                        label: '光伏站B',
                    },
                    {
                        label: '光伏站C',
                        children:[
                            {id:'rsd3',label: '光伏站1'},
                            {id:'rsd4',label: '光伏站2'},
                            {id:'rsd5',label: '光伏站3'},
                        ]
                    }
                ]
             }, 
             {
                label: '光伏项目02',
                children: [
                    {
                        label: '光伏站D',
                        children: [
                            {
                            label: '光伏站D-1'
                            }
                        ]
                   }, 
                   {
                        label: '光伏站F',
                        children: [{
                            label: '光伏站F-1'
                        }]
                   }
                ]
            }
          ],
          selecteddate:'',
          selectedcndate:'',//产能统计时间
          lastmonth:'',
          curmonth:'',
          rereshflag:false,
          minuteslist:[//刷新时间间隔列表
              {
                  'label':'1min',
                  'value':60 //60秒
              },
              {
                  'label':'5min',
                  'value':300
              },
              {
                  'label':'20min',
                  'value':1200
              }
              
          ],
          selecteminutes:60,//选中的时间间隔
          activeName:'first',
          defaultname:'first',
          tabchangeflag:false,
          stationname:'',//电站名称
          stationid:'',//电站id
          stationinfo:{
              imgurl:'',//显示图片
              zjgl:'',//装机功率
              gl:'',//功率
              ssfz:'',//瞬时辐照
              todayfdl:'',//今日发电量
              yesterdayfdl:'',//昨日发电量
              pr:'',//pr值
              equaltime:'',//等效时间
              warninfo:'',//告警信息
              messstatus:'',//通讯状态
          },
          searchtext:'',
          lastlongmonth:1,//最近几个月
          tabledata:[],//表格数据
          currentPage:1,//当前页
          pagesize:20,//每页多少条数据
          tabletotal:0,//共多少条数据，方便分页
          gfunitlist:[],//光伏单元详情列表
          dayfdl:'',//日发电量
          dayavefdl:'',//日均发电量
          equaltime:{//等效时间信息
              max:'',
              min:'',
              ave:''
          }





        };
    },
    components: {
        TreeOrg
    },
    methods: {
        PVviewById(id){//点击树
            this.tabchangeflag=false;
            if(this.activeName=='first'){
                this.getstationinfoByid();
                this.getechartinfo();
                this.getfaultinfodata();
            }else{
                this.getgflist();
                this.getcnstatisinfo();
                this.getdayfhfsinfo();
                this.getequaltimeinfo();

            }
        },
        getstationinfoByid(){//获取电站基本信息
            var that = this;
            var params={
                stationid:that.stationid
            }

            that.$http
            .post(this.BASE_INFO.BASE_URL+"api/resource/getstationinfoByid",params)
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.stationinfo=data;

                    }
                }
            });

        },
        getechartinfo(){
            var that = this;
            var params={
                date:that.selecteddate
            }

            that.$http
            .post(this.BASE_INFO.BASE_URL+"api/resource/getstationfaultbyDate",params)
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.initchart(data);

                    }
                }
            });

            

        },
        initchart(data){//产能统计
             var legendata=[];
             var xdata=[];
           
            legendata= ["功率",'累计电量'];
            for(var i=1;i<=24;i++){
                xdata.push(i+'时');
            }
          
            var predata=data.prelist;
            var curdata=data.curlist;
            var option = {
                title : {
                    text: '',
                },
                legend: {
                    data:legendata
                },
                tooltip: {
                    trigger: 'axis'
                },
                grid:{
                    x:50,
                    y:30,
                    x2:30,
                    y2:20
                },

                xAxis : [
                    {
                        type : 'category',
                        data :xdata
                    }
                ],
                yAxis : [
                    {
                        name:'单位:kW.h',
                        type : 'value'
                    },
                    {
                        name:'单位:kW',
                        type : 'value'
                    }
                ],
                series : [
                    {
                        name:legendata[0],
                        type:'line',
                        data:predata,

                    },
                    {
                        name:legendata[1],
                        type:'bar',
                        data:curdata,

                    }
                ]
            };
            var myChart = echarts.init(document.getElementById("echartbar"));
            myChart.setOption(option);


        },
        btnclick(flag){//按钮组点击事件
           this.lastlongmonth=flag;
           this.getfaultinfodata();
        },
        getfaultinfodata(){//获取故障信息表格数据

            var that = this;
            var params={
                month:that.lastlongmonth,
                pagesize:that.pagesize,
                currentPage:that.currentPage
            }

            that.$http
            .post(this.BASE_INFO.BASE_URL+"api/resource/getstationfaultlist",params)
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.tabletotal=data.total;
                        that.tabledata=data.list;
                        

                    }
                }
            });


        },
        handleCurrentChange(){//table页码切换
            console.log("currentPage:"+this.currentPage);
            this.getfaultinfodata();
        },
        getgflist(){//获取单元详情tab中光伏列表

            var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getgfunitlist")
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.gfunitlist=data;
                        
                    }
                }
            });
           

        },
        getcnstatisinfo(){//获取产能统计信息
            var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getcnstatisinfo")
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.dayfdl=data.dayfdl,
                        that.dayavefdl=data.dayavefdl,
                        that.initstatisechart(data.edata);
                        
                    }
                }
            });
             
        },
        initstatisechart(data){//初始化产能统计信息的echart
            var xdata=['1时', '2时', '3时', '4时', '5时', '6时', '7时','8时', '9时', '10时', '11时', '12时', '13时', '14时','15时', '16时', '17时', '18时', '19时', '20时', '21时','22时','23时','24时'];
            var option = {
                color: ['#3398DB'],
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid: {
                    x: 30,
                    y: 10,
                    x2: 20,
                   y2:30
                },
                xAxis : [
                    {
                        type : 'category',
                        data :xdata,
                        axisTick: {
                            alignWithLabel: true
                        }
                    }
                ],
                yAxis : [
                    {
                        type : 'value'
                    }
                ],
                series : [
                    {
                        name:'',
                        type:'bar',
                        barWidth: '60%',
                        data:data
                    }
                ]
            };
            var myChart = echarts.init(document.getElementById("cnstatisechart"));
            myChart.setOption(option);
        },
        getdayfhfsinfo(){//获取日负荷与辐射曲线
            var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getdayfhfsinfo")
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.initdayfhline(data);
                       
                        
                    }
                }
            });

        },
        initdayfhline(data){
            var xdata=['6时', '7时','8时', '9时', '10时', '11时', '12时', '13时', '14时','15时', '16时', '17时', '18时', '19时', '20时', '21时'];
            var option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    crossStyle: {
                        color: '#999'
                    }
                }
            },
            grid: {
                x: 30,
                y: 50,
                x2: 40,
                y2:20
            },
            legend: {
                data:['有功功率','辐射照度']
            },
            xAxis: [
                {
                type: 'category',
                splitLine: {show:false},
                data : xdata
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    name: 'kW',
                    axisLabel: {
                        formatter: '{value}'
                    }
                },
                {
                    type: 'value',
                    name: 'W/㎡',
                    
                    axisLabel: {
                        formatter: '{value}'
                    }
                }
            ],
            series: [
                {
                    name:'有功功率',
                    type:'bar',
                    yAxisIndex: 1,
                    data:data.fhdata
                },
                {
                    name:'辐射照度',
                    type:'bar',
                    data:data.fsdata
                },

            ]
            };
            var myChart = echarts.init(document.getElementById("fhfsline"));
            myChart.setOption(option);
        },
        getequaltimeinfo(){//获取日负荷与辐射曲线
            var that = this;
            that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getequaltimeinfo")
            .then(function(result) {
                result=result.data;
                if(result.code==0){
                    if(result.data){
                        var data=result.data;
                        that.equaltime.max=data.max;
                        that.equaltime.min=data.min;
                        that.equaltime.ave=data.ave;

                        that.initequaltimechart(data.edata);
                       
                        
                    }
                }
            });
        },
        initequaltimechart(data){
            console.dir(data);
            var xdata=['1月', '2月','3月', '4月','5月','6月','7月', '8月','9月','10月','11月', '12月'];
            var option = {
            tooltip: {
                trigger: 'axis',
                axisPointer : {    
                    type : 'shadow'
                }
            },
            color: ['#3398DB'],
            grid: {
                x: 30,
                y: 50,
                x2: 40,
                y2:20
            },
            
            xAxis: [
                {
                type: 'category',
                splitLine: {show:false},
                data : xdata
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    name: '单位:h',
                    axisLabel: {
                        formatter: '{value}'
                    }
                }
            ],
            series: [
                {
                    name:'',
                    type:'bar',
                    data:data
                }
               

            ]
            };
            var myChart = echarts.init(document.getElementById("equaltime"));
            myChart.setOption(option);
        },

        tabClick(){//tab切换
            if(this.activeName==this.defaultname){
                return;
            }else{
               this.defaultname=this.activeName;
               if(!this.tabchangeflag){//只触发一次
                    this.tabchangeflag=true;
                    this.getgflist();
                    this.getcnstatisinfo();
                    this.getdayfhfsinfo();
                    this.getequaltimeinfo();
                    
               }
               
               
            }
            
        },
        changecndate(){//
           console.log('触发change');
           this.getcnstatisinfo();
        },
        changedzdate(){
            this.getechartinfo();
        },
        controlsetinterval(){//定时期刷新
          console.log('定时期刷新');
          var that=this;
          console.log("selecteminutes:"+this.selecteminutes);
          console.log('rereshflag:'+this.rereshflag);
          if(rundetailinterval){
              clearInterval(rundetailinterval);
          }
          if(this.rereshflag){
            rundetailinterval=setInterval(function(){
                that.getechartinfo();
            },this.selecteminutes*1000);

          }
        },
        refreshclick(){//刷新按钮
            this.controlsetinterval();
            this.getechartinfo();
        }


       

    },
     created(){
         this.stationname=this.$route.query.name;//从前个页面获取的电站名称
         this.stationid=this.$route.query.stationid;//从前个页面获取的电站id

    },
    mounted(){

        var d = new Date();
        var year = d.getFullYear();
        var month = d.getMonth() + 1;
        month=month<10?'0'+month:month;
        var day=d.getDate();
        day=day<10?'0'+day:day;
        this.selecteddate=year+'-'+month+'-'+day;
        this.selectedcndate=this.selecteddate;
        this.curmonth=year+'-'+month;
        if(month>1){
            month=month-1;
        }
        else{
            year=year-1;
            month=12;
        }
        this.lastmonth=year+'-'+(month<10?'0'+month:month);


        this.getstationinfoByid();
        this.getechartinfo();
        this.getfaultinfodata();



       
        



    }
};


</script>
<style>


    .floatright{float:right;}
    .datebox{width:150px;display:inline-block;}
    .datebox .el-date-editor.el-input{width:135px;}
    .leftimg{float:left;width:120px;height:140px;margin-right:20px;}
    .leftimg img{height:100%;}
    .infoblock{float:left;width:160px;}
    .infoblock+.infoblock{margin-left:10px;}
    .infoblock div{line-height:28px;}
    .panelitem+.panelitem{margin-top:15px;}

    .panelitem .el-switch{display:inline-block;}/*兼容ie9**/
   
    .panelitem .paddingright{padding-right:20px;box-sizing:border-box;}
    .panelitem .paddingleft{padding-left:20px;box-sizing:border-box;}
    .bckf7f8f9{background:#f7f8f9;height:172px;padding:10px;box-sizing:border-box;}
    .panelitem .spanicon{font-size:30px;margin-right:15px;text-align:center;}
    .panelitem .spaniconbox{padding-left:30px;box-sizing:border-box;margin-top:10px;}
    .panelitem .spanicon.iconm-temperature{color:#108CEE;}
    .panelitem .spanicon.iconm-wholegf{color:#44DB5E;;}
    .panelitem .spanicon.iconm-weather{color:#108CEE;}
    .bckf7f8f9 .el-row{margin-top:14px;}
     #echartbar{width:100%;height:140px;}
    .searchrow.smallsearch{width:200px;float:right;margin-right:10px;}
    .gfitem{padding:20px;box-sizing:border-box;float:left;margin-top:10px;overflow:hidden;}
    .gfitem .gfimgbox{width:60px;padding-top:10px;box-sizing:border-box;float:left;margin-right:15px;}
    .gfitem .gfimgbox .iconm-pv{font-size:46px;color: #1567B3;}
    .gfitem .gfcontentbox{float:left;width:70px;}
    .gfitem .gfcontentbox div{line-height:20px;}
     .glbox{height:90px;padding-top:0;padding-bottom:0}
    .glbox span{line-height:90px;}
    .glbox+.glbox{margin-top:10px;}
    .height190{height:190px;}

   /*单元详情**/ 
    .gfstationitem.borderright{border-right:1px solid #eee;}
    .gfstationitem div{line-height:25px;text-align:center;font-size:18px;}
    .gfstationitem div+div{margin-top:5px;font-size:14px;}
    .gfstationitem div span{font-size:14px;}
    .color108CEE{color:#108CEE;}
    .margintop5{margin-top:5px!important;}
    #cnstatisechart{width:100%;height:100px;}
    #fhfsline{width:100%;height:180px;}
    #equaltime{width:100%;height:160px;}

</style>


